.movie {
  cursor: pointer;
  position:relative;
  transition: all 0.4s cubic-bezier(0.17, 0.84, 0.44, 1) !important;
    @media only screen and (max-width: $bp-small) {
      width:80%;
      margin: 0 auto;
    }

  &__title {
    font-size: 2rem;
    font-weight: 500;
    letter-spacing: 1px;
    text-decoration: underline;
    color: #fff;
    text-transform: uppercase;
    opacity: 0;
    position: absolute;
    left: 0;
    top: 0;
    margin: 1em;
    transition: all 0.4s ease-in;
    background: transparent;
   
  }

  &__noPoster{
    height:40rem;
    font-size: 1.6rem;
    color: #fff;
    background-color: var(--primary-color);
    display:flex;
    align-items:center;
    justify-content:center;
  }

  img {
    height: 40rem;
    width: 100%;
    border-radius: 20px;
    object-fit: cover;
    filter: brightness(50%);
  }


  &:hover,
  &:active {
    transform: scale(1.1);
    .movie__title {
      opacity: 1;
    }
  }
}
